#zone-about {  
  height: 500px;
  
  .zone-section{
    position: relative;  

    .block-background-image {
      height: 500px;
      
    }
  }
}

@media only screen and (max-width: 600px) {
  #zone-about {  
    height: 250px;
    
    .zone-section{
      position: relative;  
  
      .block-background-image {
        height: 250px;
        
      }
    }
  }
}

#zone-footer {
  @include grid-container;
  padding-bottom: $padding-default;

}

#zone-post-footer {
  display: none;
  
}
